.message {
    position: relative;
    align-items: flex-end;
    display: flex;
    flex-direction: column;
    gap: 5px;
}

.imageAndText {
    display: flex;
    gap: 10px;

}

.imageAndText img {
    order: 1;
    width: 60px;
}

.text {
    padding: 10px;
    border-radius: 10px;
    background: #0066CC;
}

.text:after {
    content: '';
    position: absolute;
    bottom: 23px;
    right: 60px;
    width: 0;
    height: 0;
    border-right: 10px solid transparent;
    border-left: 10px solid #0066CC;
    border-top: 10px solid transparent;
    border-bottom: 10px solid #0066CC;
    z-index: -1;
}

.name {
    color: #FFFFFF;
    font-size: 18px;
    font-weight: bold;
}

.messageText {
    color: #FFFFFF;
    font-size: 16px;
    font-weight: 400;
}

.time {
    padding: 0px 10px;
}