.greetingForm {
    padding: 10px 0px;
}

.inputAndButtonContainer {
    display: flex;
    gap: 15px;
}

.error {
    color: #cc0000;
}

.input {
    padding: 5px 10px;
    border-radius: 5px;
    border:  1px solid rgba(209, 209, 209, 1);
}

.input:focus {
    border: transparent;
}

.errorInput {
    padding: 5px 10px;
    border:  1px solid #ff0000;
    border-radius: 5px;
}

.button {
    background: #0066CC;
    width: 78px;
    height: 27px;
    padding: 0px 25px;
    color: #fff;
    border-radius: 3px;
    border: solid transparent;
}

.button:focus {
}

.button:active {
opacity: 0.7;
}

.button:disabled {
    opacity: 0.5;
}

