.button {
    width: 132px;
    height: 30px;
    background: #0059b3;
    border-radius: 5px;
    border: solid transparent;
    color: #ffffff;
    cursor: pointer;
}

.disabled {
    opacity: 0.5;
    color: #808080;
}

.secondary {
    background: transparent;
    color: #0059b3;
    border: solid #0059b3;
}

.default {
    background: #0059b3;
}

.red {
    background: #cc0000;
}

.default:hover {
    background: #0080FF;
    transition: all 0.5s ease;
}

.red:hover {
    background: rgba(255, 26, 71, 1);
    transition: all 0.5s ease
}

.secondary:hover {
    background: #E5F0FA;
    transition: all 0.5s ease
}


.default:active {
    background:#0059B2;
}

.secondary:active {
    background:#C9E5FF;
}

.red:active {
    background:#B21232;
}

.button:disabled {
    background:#004D99;
}
