.friendMessage {
    position: relative;
    display: flex;
    flex-direction: column;
    gap: 5px;
}

.friendImageAndText {
    display: flex;
    gap: 10px;
}

.friendImageAndText img {
    width: 60px;
}

.friendText {
    padding: 10px;
    border-radius: 10px;
    background: rgba(245, 247, 251, 1);
    box-shadow: 0 0;
}

.friendText:after {
    content: '';
    position: absolute;
    top: 40px;
    left: 56px;
    width: 0;
    height: 0;
    border-right: 10px solid rgba(245, 247, 251, 1);
    border-left: 10px solid transparent;
    border-top: 10px solid transparent;
    border-bottom: 10px solid rgba(245, 247, 251, 1);
    z-index: -1;
}

.friendName {
    font-size: 16px;
    font-weight: bold;
}

.friendMessageText {
    font-size: 16px;
    font-weight: 400;
}

.friendTime {
    padding: 0px 10px;
}
